import React, { useState, useEffect, useMemo, useCallback } from "react";

import Box from "@mui/material/Box";
import Step from "@mui/material/Step";
import StepContent from "@mui/material/StepContent";
import StepLabel from "@mui/material/StepLabel";
import Stepper from "@mui/material/Stepper";
import Typography from "@mui/material/Typography";
import LinearProgress from "@mui/material/LinearProgress";

import { useDispatch, useSelector } from "react-redux/es/exports";
import { get, debounce, map } from "lodash";

import {
  decrementFaqStep,
  getFaqInfo,
  incrementFaqStep,
  incrementStepByAmount,
} from "../../../../store/slice/auth/signup/faqSlice";
import BasicInfoForm from "./basic-info/BasicInfoForm";
import LicenseAndPublish from "./license-and-publish/LicenseAndPublish";
import ProfessionalDomain from "./professional-domain/ProfessionalDomain";
import ProfessionalInfo from "./professional-info/ProfessionalInfo";
import ResidentialAddress from "./residential-address/ResidentialAddress";
import SocialMediaInfo from "./social-media/SocialMediaInfo";
import TermAndConditionForm from "./terms-and-conditions/TermAndConditionForm";
import {
  getConsultantData,
  getConsultantInfo,
} from "../../../../store/slice/auth/signup/consultantInfoSlice";
import {
  getCountryList,
  getLanguageList,
  getPhoneCode,
  getSocialMediaChanel,
  getSpecialization,
} from "../../../../store/slice/common";
import translate from "../../../hooks/translate";

const QuestionStepper = () => {
  const [activeStep, setActiveStep] = useState(0);
  const dispatch = useDispatch();

  const loading = useSelector((state) => state.consultantInfo.infoLoading);
  const countryLoading = useSelector((state) => state.common.countryLoading);
  const consultantData = useSelector(getConsultantData);

  const handleNext = (index) => {
    setActiveStep(() => index);
    dispatch(incrementFaqStep());
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
    dispatch(decrementFaqStep());
  };

  const callback = useCallback(async () => {
    dispatch(getFaqInfo("CONSULTANT_SIGNUP"));
    dispatch(getPhoneCode());
    dispatch(getCountryList());
    dispatch(getLanguageList());
    dispatch(getSpecialization({ sectionName: "SPECIALIZATION" }));
    dispatch(getSocialMediaChanel({ sectionName: "SOCIAL_MEDIA_CHANNELS" }));
    await dispatch(getConsultantInfo())
      .unwrap()
      .then((result) => {
        if (result.status) {
          dispatch(incrementStepByAmount(get(result, "data.step", 0)));
          setActiveStep(get(result, "data.step", 0));
        }
      });
  }, [dispatch]);

  const debounceFn = useMemo(() => debounce(callback, 1000), [callback]);

  useEffect(() => {
    debounceFn();
  }, [debounceFn]);

  const steps = [
    {
      label: "common_basic_information",
      formData: (
        <BasicInfoForm
          handleNext={handleNext}
          handleBack={handleBack}
          consultantInfo={consultantData}
          buttonLabel={translate("common_next")}
          isEdit={false}
        />
      ),
    },
    {
      label: "consultant_residential_address",
      formData: (
        <ResidentialAddress
          handleNext={handleNext}
          handleBack={handleBack}
          consultantInfo={consultantData}
          buttonLabel={translate("common_next")}
          isEdit={false}
        />
      ),
    },
    {
      label: "consultant_professional_domain",
      formData: (
        <ProfessionalDomain
          handleNext={handleNext}
          handleBack={handleBack}
          consultantInfo={consultantData}
          buttonLabel={translate("common_next")}
          isEdit={false}
        />
      ),
    },
    {
      label: "consultant_professional_information",
      formData: (
        <ProfessionalInfo
          handleNext={handleNext}
          handleBack={handleBack}
          consultantInfo={consultantData}
          buttonLabel={translate("common_next")}
          isEdit={false}
        />
      ),
    },
    {
      label: "consultant_licence_or_certification_info_&_publications_or_links",
      formData: (
        <LicenseAndPublish
          handleNext={handleNext}
          handleBack={handleBack}
          consultantInfo={consultantData}
          buttonLabel={translate("common_next")}
          isEdit={false}
        />
      ),
    },
    {
      label: "consultant_social_media_information",
      formData: (
        <SocialMediaInfo
          handleNext={handleNext}
          handleBack={handleBack}
          consultantInfo={consultantData}
          buttonLabel={translate("common_next")}
          isEdit={false}
        />
      ),
    },
    {
      label: "common_terms_and_conditions",
      formData: (
        <TermAndConditionForm
          handleNext={handleNext}
          handleBack={handleBack}
          consultantInfo={consultantData}
          buttonLabel={translate("common_next")}
          isEdit={false}
        />
      ),
    },
  ];

  return (
    <>
      <Box component="div">
        <Stepper activeStep={activeStep} orientation="vertical">
          {!loading || !countryLoading ? (
            map(steps, (step, index, { length }) => (
              <Step key={step.label}>
                <StepLabel
                  optional={
                    index === length - 1 ? (
                      <Typography variant="caption">
                        {translate("common_last_step")}
                      </Typography>
                    ) : null
                  }
                >
                  {translate(step?.label)}
                </StepLabel>
                <StepContent>
                  <Box>{step.formData}</Box>
                </StepContent>
              </Step>
            ))
          ) : (
            <LinearProgress />
          )}
        </Stepper>
      </Box>
    </>
  );
};

export default QuestionStepper;
