import AddIcon from "@mui/icons-material/Add";
import HorizontalRuleIcon from "@mui/icons-material/HorizontalRule";
import IconButton from "@mui/material/IconButton";
import Typography from "@mui/material/Typography";
import React, { useState } from "react";

import { useSelector } from "react-redux/es/exports";

import { getFaqData } from "../../../../../store/slice/auth/signup/faqSlice";
import translate from "../../../../hooks/translate";
import CMAccordion from "../../../../ui-components/accordion/CMAccordion";
import CMAccordionDetails from "../../../../ui-components/accordion/CMAccordionDetails";
import CMAccordionSummary from "../../../../ui-components/accordion/CMAccordionSummary";

const Faq = () => {
  const faqStore = useSelector(getFaqData);
  const faqData = faqStore.faqData;
  const faqStep = faqStore.faqStep;

  const [expanded, setExpanded] = useState("panel0");

  const handleChange = (panel) => (event, newExpanded) => {
    setExpanded(newExpanded ? panel : false);
  };

  return (
    <>
      <Typography variant="h4" gutterBottom>
        {translate("common_faq")}
      </Typography>
      {faqData
        .filter((data) => data.step === faqStep)
        .map((item, index) => (
          <CMAccordion
            expanded={expanded === `panel${index}`}
            onChange={handleChange(`panel${index}`)}
            key={index}
          >
            <CMAccordionSummary
              aria-controls="panel1d-content"
              id="panel1d-header"
              expandIcon={
                expanded === `panel${index}` ? (
                  <IconButton sx={{ backgroundColor: "primary.contrastText" }}>
                    <HorizontalRuleIcon />
                  </IconButton>
                ) : (
                  <IconButton>
                    <AddIcon />
                  </IconButton>
                )
              }
            >
              <Typography variant="subtitle1">
                {translate(item?.titleMacro)}
              </Typography>
            </CMAccordionSummary>

            <CMAccordionDetails>
              <Typography variant="body1" color="text.secondary">
                {translate(item?.descriptionMacro)}
              </Typography>
            </CMAccordionDetails>
          </CMAccordion>
        ))}
    </>
  );
};

export default Faq;
