import React from "react";

import Box from "@mui/material/Box";
import Skeleton from "@mui/material/Skeleton";

const FaqSkeleton = () => {
  return (
    <>
      <Box component="div" sx={{ width: "100%"}} >
        <Skeleton animation="wave" height={50}/>
        <Skeleton animation="wave" height={50}/>
        <Skeleton animation="wave" height={50}/>
        <Skeleton animation="wave" height={50}/>
      </Box>
    </>
  );
};

export default FaqSkeleton;
