import React from "react";

import Box from "@mui/material/Box";
import Container from "@mui/material/Container";
import Grid from "@mui/material/Grid";

import { useSelector } from "react-redux";

import Faq from "../../../components/auth/signup/consultant-info/faq/Faq";
import QuestionStepper from "../../../components/auth/signup/consultant-info/QuestionStepper";
import FaqSkeleton from "../../../components/ui-components/skeleton/FaqSkeleton";

const ConsultantInfo = () => {
  const loading = useSelector((state) => state.faq.loading);
  return (
    <>
      <Box
        component="section"
        sx={{ py: { sm: 6.4, xs: 4 } }}
        className="d-flex align-items-center justify-content-center w-100"
      >
        <Container maxWidth="xl">
          <Grid
            container
            spacing={2}
            sx={{ justifyContent: { md: "space-between", xs: "center" } }}
            column={12}
          >
            <Grid item md={6} xs={12}>
              <QuestionStepper />
            </Grid>
            <Grid item lg={5} md={6}>
              {loading ? <FaqSkeleton /> : <Faq />}
            </Grid>
          </Grid>
        </Container>
      </Box>
    </>
  );
};

export default ConsultantInfo;
