import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import FormControlLabel from "@mui/material/FormControlLabel";
import FormGroup from "@mui/material/FormGroup";
import FormHelperText from "@mui/material/FormHelperText";
import Grid from "@mui/material/Grid";
import Stack from "@mui/material/Stack";

import { Formik } from "formik";
import { get } from "lodash";
import { useCookies } from "react-cookie";
import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import * as Yup from "yup";

import lastStepImg from "../../../../../assets/images/last_step.svg";
import { getMeInfo } from "../../../../../store/slice/auth/authentication/authSlice";
import {
  getConsultantInfo,
  postConsultantInfo
} from "../../../../../store/slice/auth/signup/consultantInfoSlice";
import { expireTime } from "../../../../hooks/cookie";
import translate from "../../../../hooks/translate";
import { DoubleCheckIcon } from "../../../../Icon";
import CMCheckBox from "../../../../ui-components/form-components/CMCheckBox";

const TermAndConditionForm = (props) => {
  const { handleBack, consultantInfo } = props;
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [, setCookie] = useCookies();
  const store = consultantInfo;
  const consultantData = store.data;

  const Schema = Yup.object().shape({
    termsAndCondition: Yup.boolean().oneOf(
      [true],
      "Must Accept Terms and Conditions"
    ),
  });

  const handleTermsCondition = async (payload) => {
    const res = await dispatch(postConsultantInfo(payload))
      .unwrap()
      .then(async (result) => {
        if (result.status) {
          dispatch(getConsultantInfo());
          await dispatch(getMeInfo())
            .unwrap()
            .then((result) => {
              if (result.status) {
                setCookie("isSignupCompleted", result.data.termsAndCondition, {
                  path: "/",
                  expires: expireTime,
                });
              }
            })
            .catch((error) => {
              console.log("OTP error=======>", error);
            });
          navigate("/dashboard");
        }
      })
      .catch((err) => {
        console.log(" basic err", err);
      });
    return res;
  };
  return (
    <>
      <Box>
        <Formik
          initialValues={{
            termsAndCondition: get(consultantData, "termsAndCondition", false),
            subscription: get(consultantData, "subscription", false),
          }}
          validationSchema={Schema}
          onSubmit={async (values) => {
            const payload = {
              termsAndCondition: values.termsAndCondition,
              subscription: values.subscription,
              step: 6,
            };
            await handleTermsCondition(payload);
          }}
        >
          {(props) => (
            <form onSubmit={props.handleSubmit}>
              <Grid container spacing={2}>
                <Grid item xs={12}>
                  <Box
                    component="img"
                    src={lastStepImg}
                    className="img-fluid w-100"
                  />
                  <FormGroup>
                    <Box className="checkbox_wrapper">
                      <FormControlLabel
                        control={
                          <CMCheckBox
                            name="termsAndCondition"
                            checked={props.values.termsAndCondition}
                            checkedIcon={<DoubleCheckIcon />}
                            onChange={props.handleChange}
                          />
                        }
                        label={translate("common_terms_and_conditions")}
                      />
                    </Box>
                    {props.errors.termsAndCondition && (
                      <FormHelperText error={true}>
                        {props.errors.termsAndCondition}
                      </FormHelperText>
                    )}
                    {/* <Box className="checkbox_wrapper mt-1">
                      <FormControlLabel
                        control={
                          <CMCheckBox
                            name="subscription"
                            checked={props.values.subscription}
                            checkedIcon={<DoubleCheckIcon />}
                            onChange={props.handleChange}
                          />
                        }
                        label={translate("common_subscription")}
                      />
                    </Box> */}
                    {props.errors.subscription && (
                      <FormHelperText error={true}>
                        {props.errors.subscription}
                      </FormHelperText>
                    )}
                  </FormGroup>
                </Grid>

                <Grid item xs={12}>
                  <Stack direction="row" justifyContent="space-between">
                    <Button
                      type="button"
                      variant="outlined"
                      color="primary"
                      onClick={handleBack}
                    >
                      {translate("common_btn_back")}
                    </Button>
                    <Stack direction="row" spacing={1}>
                      <Button
                        type="submit"
                        variant="contained"
                        disabled={props.isSubmitting}
                      >
                        {translate("common_next")}
                      </Button>
                    </Stack>
                  </Stack>
                </Grid>
              </Grid>
            </form>
          )}
        </Formik>
      </Box>
    </>
  );
};

export default TermAndConditionForm;
